body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.meal_tile {
    width: 250px;
    overflow: hidden;
    position: relative;
    margin: 20px;
    padding: 0px;
    display: inline-block;
}

.meal_tile img{
    width: 100%;
}

.meal_tile p {
    position: absolute;
    bottom: 0;
    margin: 10px;
    background-color: rgba(255,255,255,0.75);
    padding: 5px;
    font-size: 0.75em;
}

.header {
    padding: 20px;
}

button {
    background-color: lightgrey;
    border: 0px;
    margin: 10px;
    padding: 2px 10px 2px 10px;
}

input {
    border: 1px solid lightgrey;
    margin: 10px;
}

button:hover {
    background-color: grey;
}

#title-block {
    text-align: center;
    height: 250px;
    background-image: url(/meal-ideas-app/static/media/header-image.b7885cad.jpg);
    background-size: 100%;
    background-repeat: no-repeat;
    background-position-x: center;
    padding: 0px;
    color: white;
    margin-bottom: 0;
    padding-bottom: 0;
}

#title-block h1, p {
    padding: 20px;
    margin-bottom: 50px;
    background-color: rgba(0,0,0,0.5);
}

#title-block h1 {
    margin-top: 0;
}

body {
    min-width: 750px;
}

form {
    padding: 20px;
    padding-left: 0;
    padding-bottom: 0;
    margin-bottom: 0;
}

h2 {
    padding: 0 20px 0 20px;
    margin-top: 0;
}
